@import "../../shared/styles/constants.css";

.wrapper {
  position: relative;
  width: 380px;
  height: 460px;
  padding: 28px 36px 36px;

  box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);
  border-radius: 20px;

  background-image: url(../../images/hero-picture.png),
    url(../../images/logo.png),
    linear-gradient(114.99deg, #471ca9 -0.99%, #5736a3 54.28%, #4b2a99 78.99%);
  background-repeat: no-repeat;
  background-position: 36px 28px, 20px 20px, 0 0;
}

.divider {
  position: absolute;
  width: 380px;
  height: 8px;
  left: 0px;
  top: 214px;

  background-color: var(--accent-text-color);
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.06),
    inset 0px -1.71846px 3.43693px #ae7be3, inset 0px 3.43693px 2.5777px #fbf8ff;
}

.thumb {
  position: absolute;
  width: 80px;
  height: 80px;
  left: 150px;
  top: 178px;

  border: 8px solid var(--accent-text-color);
  border-radius: 50%;
}

.thumb::before {
  content: "";
  width: 80px;
  height: 80px;
  position: absolute;
  top: -8px;
  left: -8px;
  box-shadow: 0px 4.39163px 4.39163px rgba(0, 0, 0, 0.06),
    inset 0px -2.19582px 4.39163px #ae7be3,
    inset 0px 4.39163px 3.29372px #fbf8ff;
  border-radius: 50%;
}

.avatar {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.infoBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 256px;
}

.tweets {
  margin-bottom: 16px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
  text-transform: uppercase;
  color: var(--accent-text-color);
}

.followers {
  margin-bottom: 26px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
  text-transform: uppercase;
  color: var(--accent-text-color);
}

.button {
  width: 196px;
  padding: 14px;

  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
  border: none;
  border-radius: 10.3108px;

  font-weight: 600;
  font-size: 18px;
  line-height: 1.22;
  text-transform: uppercase;
  color: var(--primary-text-color);
}

.btnFollow {
  background-color: var(--accent-text-color);
}

.btnFollowing {
  background-color: var(--active-color);
}
